.formBox {
    width: 96%;
    margin: 0 auto;
    padding: 30px 17px;
    background-color: rgba(0, 0, 0, 0.5);
}

.link {
    display: block;
    width: 320px;
    padding: 4px 8px;
    font-size: 18px;
    margin-bottom: 12px;
    border: 1px solid var(--color-main);
    box-shadow: 0px 6px 7px -4px #636363;
    border-radius: 4px;
    color: var(--color-secondary);
}